<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__window" @click.stop>
        <h2 class="modal__title">
          {{ edit ? words.titlewindowedit[lang] : words.titlewindow[lang] }}
        </h2>
        <div class="modal__inputs">
          <label>
            <span>Title</span>
            <input v-model="title" type="text" placeholder="Title" />
          </label>
          <label>
            <span>Content</span>
            <input v-model="text" type="text" placeholder="Content" />
          </label>
        </div>
        <div class="modal__buttons">
          <button class="modal_btn del" @click="closeModal">
            {{words.closebtn[lang]}}
          </button>
          <button v-if="edit" class="modal_btn edit" @click="changedNote">
            {{ words.editbtn[lang] }}
          </button>
          <button v-else class="modal_btn edit" @click="addNote">
            {{words.addbtn[lang]}}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  data() {
    return {
      title: null,
      text: null,
      id: 4,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.title = null;
      this.text = null;
    },
    addNote() {
      if (this.title && this.text) {
        const item = {
          id: this.id++,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.closeModal();
      }
    },
    changedNote() {
      if (this.title != null && this.text != null) {
        let newNote = {
          id: this.editNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("changedNote", newNote);
        this.closeModal();
      }
    },
  },
  mounted() {
    if (this.edit) {
      this.title = this.editNote.title;
      this.text = this.editNote.text;
    }
  },
  props: {
    currentId: Number,
    edit: Boolean,
    editNote: Object,
    lang:String
  },
  inject:['words']
};
</script>

