<template>
  <Navbar
    @searchValue="search = $event"
    :lang="lang"
    @changeLang="changeLang"
  />
  <Notes :notes="filterNotes" @delNote="deleteNote" @changeNote="changeNote" :lang="lang"/>
  <AddButton @openModal="modal = true" />
  <Modal
    v-if="modal"
    @closeModal="closeModal"
    @addNote="addNewNote"
    :edit="edit"
    :editNote="editNote"
    @changedNote="changedNote"
    :lang="lang"
  />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddButton from "@/components/AddButton.vue";
import Modal from "@/components/Modal.vue";
import Langs from "./lang.js";
export default {
  components: {
    Navbar,
    Notes,
    AddButton,
    Modal,
  },
  data() {
    return {
      modal: false,
      edit: false,
      editNote: {},
      notes: [
        {
          id: 1,
          title: "Vue",
          text: "Vue bu eng kichik hajmli framework",
          date: "25.06.2011",
        },
        {
          id: 2,
          title: "React",
          text: "React bu eng yaxshi framework",
          date: "31.01.2017",
        },
        {
          id: 3,
          title: "Angular",
          text: "Angular bu eng katta hajmli framework",
          date: "13.07.2015",
        },
      ],
      search: null,
      lang: "ru",
      langWords: {},
    };
  },
  methods: {
    changeLang(value) {
      this.lang = localStorage.lang = value;
    },
    changeNote(id) {
      this.edit = this.modal = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
    },
    addNewNote(newNote) {
      this.notes.push(newNote);
    },
    closeModal() {
      this.modal = false;
      setTimeout(() => {
        this.edit = false;
      }, 500);
    },
    getNotes() {
      let localNotes = localStorage.getItem("notes");
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
        this.currentId = this.notes.length + 1;
      }
    },
    deleteNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
    },
    changedNote(newNote) {
      this.notes.forEach((note) => {
        if (note.id == newNote.id) {
          note.title = newNote.title;
          note.text = newNote.text;
          note.date = newNote.date;
        }
      });
    },
  },
  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },
  mounted() {
    if (this.edit) {
      this.title = this.editNote.title;
      this.text = this.editNote.text;
    }
  },
  created() {
    this.getNotes();
    this.lang = localStorage.lang = localStorage.lang || "ru";
    localStorage.words = JSON.stringify(Langs);
  },
  provide() {
    return {
      words: localStorage.words ? JSON.parse(localStorage.words) : Langs
    };
  },
  watch: {
    notes: {
      handler(newNote) {
        localStorage.setItem("notes", JSON.stringify(newNote));
      },
      deep: true,
    },
  },
};
</script>

